import { getComments } from '@/api/articles'
export default {
  name: 'ArticleComments',
  props: {
    article: {
      type: Object,
      required: true,
    },
  },
  data() {
    return {
      comments: [],
    }
  },
  async mounted() {
    const { data } = await getComments(this.article.slug)
    // console.log('data', data)
    this.comments = data.comments
  },
}
